*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

:root {
  --title-text-color: #374a59;
  --light-text-color: #fbfbfb;
  --text-white-color: #fff;
  --primary-text-color: #434455;
  --text-footer-white: #e7e9fc;
  --input-grey: #d8dbdd;
  --phone-grey: #738593;
  --accent-color: #76b703;
  --icons-bg: #ccc;
  --primary-bg-color: #fff;
  --footer-bg-color: #253a4a;
  --second-bg-color: #ebebeb;
  --primary-fonts-family: "Ubuntu";
  --second-fonts-family: sans-serif;
  --header-idx: 100;
  --modal-idx: 1000;
  --anim-fast: 1300ms ease;
  --anim-slow: 1800ms ease-out;
}

body {
  font-family: var(--primary-fonts-family);
  background-color: var(--primary-bg-color);
  color: var(--primary-text-color);
  margin: 0;
  font-size: 18px;
  line-height: 1.5;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

h1 {
  font-size: 27px;
}

h2 {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 24px;
}

h3 {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 20px;
}

.link {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.button {
  font-family: var(--second-fonts-family);
  cursor: pointer;
  background-color: var(--accent-color);
  text-align: center;
  letter-spacing: .04em;
  border: 0;
  border-radius: 2px;
  align-items: center;
  margin: 0 auto;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.18;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.button-header {
  color: #fbfbfb;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 15px 30px;
  display: flex;
}

.button-header:hover {
  background-color: var(--footer-bg-color);
}

.button-footer {
  color: #fbfbfb;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 15px 30px;
  font-size: 18px;
  display: inline-flex;
}

.button-footer:hover {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(255, 255, 255, .3);
}

.button-footer:focus {
  border: 1px #ccc;
}

.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: visibility .5s, opacity .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.8);
}

.modal {
  width: calc(100% - 30px);
  max-width: 320px;
  background-color: var(--second-bg-color);
  border-radius: 4px;
  padding: 60px 25px 45px 30px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%)scale(1);
}

.menu-btn-burger {
  fill: var(--title-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: none;
  right: 15px;
}

.menu-btn-close {
  height: 34px;
  width: 34px;
  cursor: pointer;
  background: var(--accent-color);
  color: var(--primary-bg-color);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 30px;
  right: 30px;
}

.menu-btn-close:hover {
  background: var(--primary-bg-color);
  color: var(--title-text-color);
}

.menu-toggle:hover, .menu-toggle:focus {
  fill: var(--accent-color);
}

.mobile-menu {
  margin-bottom: auto;
}

.mobile-menu-item:not(:last-child) {
  margin-bottom: 15px;
}

.mobile-menu-link {
  color: var(--title-text-color);
  letter-spacing: .02em;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.11;
  display: block;
}

.mobile-menu-link:hover {
  color: var(--accent-color);
}

@media screen and (max-width: 793px) {
  .menu-toggle {
    display: flex;
  }
}

@media screen and (max-width: 475px) {
  .modal {
    padding: 30px 25px 30px 30px;
    top: 2%;
  }

  .menu-btn-close {
    top: 15px;
    right: 15px;
  }
}

.submenu-open {
  max-width: 230px;
  transform-origin: 0 0;
  background-color: var(--accent-color);
  border-radius: 3px;
  margin: 15px auto;
  transition: all .3s linear;
}

.submenu-open a {
  color: var(--text-white-color);
  padding: 10px 10px 10px 15px;
  font-size: 18px;
  line-height: 1.2;
  text-decoration: none;
  display: block;
}

.submenu-open a:hover {
  color: var(--title-text-color);
}

.hiddens {
  display: none;
}

.main-nav {
  padding-top: 14px;
}

.nav-contacts {
  border-bottom: 1px solid #000;
  align-items: self-start;
  display: flex;
}

.number {
  display: flex;
}

header {
  background-color: var(--second-bg-color);
  font-family: var(--second-fonts-family);
  color: var(--title-text-color);
}

.nav__upp {
  display: flex;
}

.language-switcher {
  color: var(--title-text-color);
  background-color: inherit;
  margin: 0 auto;
  font-size: 16px;
}

.contact-nav {
  max-height: 80px;
  margin-left: auto;
  display: flex;
}

.input__number {
  background-color: var(--input-grey);
  color: var(--title-text-color);
  border: none;
  border-radius: 1px;
  padding: 15px 30px;
  font-size: 18px;
  display: block;
}

.input__number::-ms-input-placeholder {
  color: var(--title-text-color);
  font-size: 18px;
}

.input__number::placeholder {
  color: var(--title-text-color);
  font-size: 18px;
}

.contact-nav-link {
  letter-spacing: .04em;
  color: var(--title-text-color);
  font-size: 36px;
  font-weight: 500;
  line-height: 1.33;
  font-family: var(--second-fonts-family);
  padding-left: 24px;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.contact-nav-link span {
  color: var(--phone-grey);
}

.contact-nav-link:hover, .contact-nav-link:focus {
  color: var(--accent-color);
}

.icon-right-header {
  stroke: #fff;
}

.icon-right-header:hover {
  stroke: var(--accent-color);
}

.site-nav {
  justify-content: end;
  gap: 40px;
  display: flex;
}

.site-nav .nav-link {
  color: var(--title-text-color);
  text-transform: uppercase;
  padding-top: 24px;
  padding-bottom: 25px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.site-nav .nav-link:hover {
  color: var(--accent-color);
  stroke: var(--accent-color);
}

.site-nav-item {
  padding-left: 10px;
  padding-right: 10px;
  display: block;
  position: relative;
}

.site-nav-menu {
  text-transform: uppercase;
  color: var(--title-text-color);
  stroke: var(--title-text-color);
  align-items: center;
  padding-top: 24px;
  padding-bottom: 25px;
  font-size: 16px;
  display: flex;
}

.site-nav-menu:hover {
  color: var(--accent-color);
  stroke: var(--accent-color);
}

.submenu {
  width: 180px;
  transform-origin: 0 0;
  visibility: hidden;
  opacity: 0;
  z-index: 10;
  line-height: 1.33;
  transition: all .3s linear;
  position: absolute;
  top: 100%;
  left: 0;
}

.site-nav li:hover > .submenu {
  visibility: visible;
  opacity: 1;
}

.submenu a {
  color: var(--title-text-color);
  border-bottom: 1px solid var(--phone-grey);
  background-color: var(--second-bg-color);
  padding: 5px 5px 5px 15px;
  font-size: 14px;
  text-decoration: none;
  display: block;
}

.submenu a:hover {
  color: var(--accent-color);
}

.arrowCaretUp {
  transform: rotate(180deg);
}

@media screen and (max-width: 1200px) {
  .nav-contacts {
    margin-top: 20px;
  }

  .contact-nav {
    flex-direction: column-reverse;
  }

  .header-phone {
    text-align: right;
    display: block;
  }

  .site-nav {
    justify-content: space-between;
    gap: 20px;
    margin-left: auto;
  }
}

@media screen and (max-width: 1100px) {
  .submenu {
    left: -10px;
  }

  .site-nav li:hover > .submenu {
    width: 150px;
  }
}

@media screen and (max-width: 980px) {
  .site-nav {
    gap: 0;
  }

  .button-header {
    font-size: 16px;
  }

  .input__number::-ms-input-placeholder {
    font-size: 16px;
  }

  .input__number::placeholder {
    font-size: 16px;
  }

  .input__number {
    font-size: 16px;
  }
}

@media screen and (max-width: 875px) {
  .site-nav .nav-link, .site-nav-menu, .language-switcher {
    font-size: 14px;
  }
}

@media screen and (max-width: 793px) {
  .site-nav {
    display: none;
  }

  .phone-burger {
    display: flex;
  }

  .nav-contacts {
    border-bottom: none;
    padding-bottom: 10px;
  }

  .contact-nav {
    max-height: 120px;
  }
}

@media screen and (max-width: 787px) {
  .logo__header-img {
    max-width: 130px;
  }

  .nav-contacts {
    justify-content: space-between;
  }

  .input__number {
    max-width: 185px;
    padding: 10px 20px;
    font-size: 14px;
  }

  .input__number::-ms-input-placeholder {
    padding: 10px 20px;
    font-size: 14px;
  }

  .input__number::placeholder {
    padding: 10px 20px;
    font-size: 14px;
  }

  .button-header {
    padding: 10px 20px;
    font-size: 14px;
  }

  .language-switcher {
    font-size: 14px;
  }
}

@media screen and (max-width: 490px) {
  .input__number {
    max-width: 150px;
    padding-left: 15px;
  }

  .input__number::-ms-input-placeholder {
    padding-left: 0;
  }

  .input__number::placeholder {
    padding-left: 0;
  }

  .header-phone {
    font-size: 26px;
  }
}

@media screen and (max-width: 455px) {
  .number {
    justify-content: flex-end;
  }

  .icon-right-header {
    display: none;
  }

  .header-phone {
    font-size: 24px;
  }

  .input__number {
    max-width: 105px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 11px;
  }

  .input__number::-ms-input-placeholder {
    padding-left: 5px;
    font-size: 11px;
  }

  .input__number::placeholder {
    padding-left: 5px;
    font-size: 11px;
  }

  .button-header {
    margin-left: 0;
    margin-right: 0;
    font-size: 11px;
  }

  .contact-nav-link {
    padding-left: 0;
  }
}

@media screen and (max-width: 370px) {
  .icon-right-header {
    display: flex;
  }

  .button-header > span {
    display: none;
  }

  .button-header {
    margin: 0;
  }

  .header-phone {
    font-size: 20px;
  }
}

@media screen and (max-width: 345px) {
  .header-phone {
    font-size: 18px;
  }
}

.footer {
  background-color: var(--footer-bg-color);
  color: var(--text-footer-white);
}

.footer-content {
  font-size: 11px;
  font-size: sans-serif;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 25px;
  padding-bottom: 40px;
  display: flex;
}

.footer-content a {
  text-decoration: none;
}

.footer__logo-text {
  width: 25%;
  padding: 0 20px;
}

.logo__footer-img {
  margin-bottom: 15px;
}

.footer-text {
  margin: 0 0 15px;
}

.footer__list-menu {
  justify-content: center;
  display: flex;
}

.footer__list-moto, .footer__list-service, .footer__list-contacts {
  padding-left: 10px;
  padding-right: 10px;
}

.footer__list-title > h3 {
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 16px;
}

.footer__list-item {
  margin-bottom: 16px;
}

.footer__list-item a:hover {
  color: var(--accent-color);
}

.footer__list-item a {
  color: var(--text-footer-white);
}

.footer__contacts-social {
  width: 25%;
  text-align: right;
  justify-content: flex-end;
  display: block;
}

.item-tel {
  margin-bottom: 50px;
}

.item-tel a {
  color: var(--text-footer-white);
}

.footer-phone span {
  color: var(--icons-bg);
}

.footer-soc-list {
  text-align: right;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
  display: flex;
}

.footer-soc-link {
  width: 32px;
  height: 32px;
  background-color: var(--icons-bg);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.footer-soc-link:hover, .footer-soc-link:focus {
  background-color: var(--accent-color);
}

.footer-soc-icon {
  width: 20px;
  height: 20px;
}

.icon-right-footer {
  stroke: #fff;
}

.icon-right-footer:hover {
  stroke: var(--accent-color);
}

@media screen and (max-width: 1200px) {
  .footer__contacts-social {
    width: 100%;
    display: block;
  }

  .contact-nav-link {
    text-align: right;
  }

  .item-button {
    color: #fbfbfb;
    max-width: 150px;
    padding-left: 0;
    display: inline-block;
  }

  .item-tel {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 980px) {
  .button-footer {
    font-size: 16px;
  }
}

@media screen and (max-width: 787px) {
  .button-footer {
    padding: 10px 20px;
    font-size: 14px;
  }
}

@media screen and (max-width: 777px) {
  .footer__logo-text {
    width: 60%;
    padding: 0 20px 20px;
  }

  .footer__list-menu {
    width: 100%;
    gap: 20px;
  }

  .item-tel, .footer-soc-list {
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 680px) {
  .footer__list-title > h3 {
    font-size: 14px;
  }

  .footer__logo-text {
    width: 80%;
  }

  .footer__list-menu {
    gap: 10px;
  }
}

@media screen and (max-width: 500px) {
  .footer__logo-text {
    width: 100%;
  }

  .footer__list-menu {
    gap: 0;
  }

  .footer__list-moto, .footer__list-service, .footer__list-contacts {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 500px) {
  .footer-phone {
    font-size: 23px;
  }
}

@media screen and (max-width: 455px) {
  .button-footer {
    padding: 10px;
    font-size: 12px;
  }
}

.hero-section {
  background: linear-gradient(rgba(25, 28, 38, .61), rgba(25, 28, 38, .61));
  margin: 0 auto;
}

.main-swiper {
  padding-bottom: 70px;
  position: relative;
  overflow: hidden;
}

.hero_main .swiper-slide {
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 220px;
}

.slide1 {
  background-image: linear-gradient(rgba(25, 28, 38, .6), rgba(25, 28, 38, .6)), url("glavnaya1.8276da87.gif");
}

.slide2 {
  background-image: linear-gradient(rgba(25, 28, 38, .6), rgba(25, 28, 38, .6)), url("ezgif.com-optimize_1.c2a2eeb1.gif");
}

.slide3 {
  background-image: linear-gradient(rgba(25, 28, 38, .6), rgba(25, 28, 38, .6)), url("ezgif.com-optimize_2.ff033cb0.gif");
}

.slide4 {
  background-image: linear-gradient(rgba(25, 28, 38, .6), rgba(25, 28, 38, .6)), url("ezgif.com-optimize_4.a7da2e58.gif");
}

.slide5 {
  background-image: linear-gradient(rgba(25, 28, 38, .6), rgba(25, 28, 38, .6)), url("copy_5_c.befc60fe.gif");
}

.hero__descr {
  max-width: 400px;
  color: var(--text-white-color);
  padding-left: 20px;
}

.hero__title {
  text-transform: uppercase;
  padding-bottom: 30px;
  font-size: 36px;
  font-weight: 500;
  animation: bottom-to-top 2s ease-in-out both;
}

@keyframes bottom-to-top {
  0% {
    opacity: 0;
    transform: translateY 200px;
  }

  50% {
    opacity: .6;
  }

  100% {
    opacity: 1;
    transform: translateY 0;
  }
}

.hero__text {
  padding-bottom: 30px;
  font-size: 24px;
}

.hero__button {
  background-color: var(--accent-color);
  color: var(--light-text-color);
  margin-left: 0;
  padding: 15px 30px;
  font-size: 18px;
  text-decoration: none;
  display: inline-block;
}

.hero__button:hover {
  background-color: var(--footer-bg-color);
}

.hero__item-img-wrap {
  margin-right: 15px;
}

.main-swiper .swiper-pagination {
  margin-bottom: 20px;
}

.swiper-pagination-bullet {
  border-color: var(--footer-bg-color);
  width: 53px;
  height: 5px;
  border-radius: 15px;
}

.swiper-pagination-bullet-active {
  background: var(--accent-color);
}

@media screen and (max-width: 1180px) {
  .hero__descr {
    padding-right: 25px;
  }

  .hero__item-img-wrap {
    margin-left: auto;
  }
}

@media screen and (max-width: 1000px) {
  .hero__title {
    padding-bottom: 20px;
    font-size: 30px;
    line-height: 1.2;
  }

  .hero__text {
    padding-bottom: 30px;
    font-size: 20px;
  }

  .hero__button {
    padding: 15px 30px;
    font-size: 16px;
  }

  .hero__item-img-wrap {
    margin-left: 0;
  }
}

@media screen and (max-width: 787px) {
  .hero__descr {
    min-width: 250px;
  }

  .hero__title {
    padding-bottom: 10px;
    font-size: 24px;
  }

  .main-swiper .swiper-pagination {
    bottom: 20px;
  }

  .hero__button {
    padding: 15px;
    font-size: 14px;
  }
}

